.b-cabinet {}
.b-cabinet__form {
    @include row-flex(); }

.b-cabinet__form-inner {
    @include col();
    @include size(8);
    @include row-flex();
    margin-top: -15px;
    margin-bottom: -15px;
    margin-left: 0;
    margin-right: 0;
    justify-content: flex-start;
    align-items: flex-start;

    @include to(1400px) {
        @include size(6); }

    @include lg-block() {
        width: 100%; }

    @include sm-block() {
        margin-top: -10px;
        margin-bottom: -10px; } }


.b-cabinet__form-upload {
    @include col();
    width: calc(100% / 3);

    @include to(1400px) {
        width: calc(100% / 2); }

    @include lg-block() {
        order: -1;
        width: 100%;
        margin-bottom: 30px; } }

.b-cabinet__form-field {
    @include col();
    @include size(6);
    margin-top: 15px;
    margin-bottom: 15px;

    @include to(1400px) {
        @include size(12); }

    @include sm-block() {
        margin-top: 10px;
        margin-bottom: 10px; }

    @include xs-block() {
        margin-top: 10px;
        margin-bottom: 10px; } }


.b-cabinet__form-footer {
    @include col();
    @include size(12);
    @include size-lg(12);
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    margin-top: 15px;
    margin-bottom: 15px; }

.b-cabinet__form-redirect-link {
    margin-left: 10px; }

.b-cabinet__form-remember {
    margin-left: auto;

    @include to(1200px) {
        order: -1;
        margin-left: initial; } }

.b-cabinet__form-submit {
    margin-left: 30px;

    @include to(1200px) {
        margin: 15px 0 30px 0;
        width: 100%; } }

.b-cabinet__photos {
    @include row-flex();
    margin-top: -15px;
    margin-bottom: -15px; }

.b-cabinet__photo {
    margin-top: 15px;
    margin-bottom: 15px;
    @include col();
    @include size(4);

    @include from-to($break-md, 1400px) {
        @include size(6); }

    @include size-md(12);

    @include size-sm(6);

    @include size-xs(12); }

.b-cabinet__add-photo {
    margin-top: 30px; }

.b-cabinet__table {
    width: 100%;
    max-width: 100%;
    overflow-y: auto;
    box-shadow: 0 5px 10px rgba(0,0,0,.1);
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px; }
