.b-pagination-model {}
.b-pagination-model__items {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: -15px 0;

    @include md-block() {
        flex-direction: column;
        justify-content: center;
        flex-wrap: wrap; }

    @include xs(margin, -10px); }


.b-pagination-model__item {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    position: relative;
    margin: 15px 0;

    @include lg(font-size, 14px);
    @include xs(margin, 10px); }

.b-pagination-model__item-line {
    content: '';
    height: 8px;
    width: 40%;
    @include absolute(auto, auto, 2px, 15px);
    background: linear-gradient(90deg, $firstGradientStart, $firstGradientEnd);

    @include to(1200px) {
        height: 6px;
        bottom: 1px; }

    @include lg-block() {
        left: 10px; }

    @include md-block() {
        height: 8px;
        bottom: 2px; }

    @include sm(height, 6px);
    @include xs(bottom, 1px); }

.b-pagination-model__item-icon {
    font-size: 36px;
    @include gradient-text(0deg, $firstGradientStart, $firstGradientEnd);

    @include to(1600px) {
        font-size: 28px; }

    @include to(1400px) {
        font-size: 22px; }

    @include to(1200px) {
        font-size: 16px; }

    @include md(font-size, 28px);
    @include sm(font-size, 24px);
    @include xs(font-size, 20px); }

.b-pagination-model__item-text {
    position: relative;
    @include text($darkColor, 36px, 900, 1);
    margin: 0 15px;

    @include to(1600px) {
        font-size: 28px; }

    @include to(1400px) {
        font-size: 22px; }

    @include to(1200px) {
        font-size: 16px; }

    @include lg-block() {
        margin: 0 10px; }

    @include md(font-size, 28px);
    @include sm(font-size, 24px);
    @include xs(font-size, 20px); }

.b-pagination-model__item_prev {
    @include md(order, 2); }

.b-pagination-model__item_current {
    @include md(order, 1); }

.b-pagination-model__item_next {
    @include md(order, 3); }
