.b-like {
    display: flex;
    flex-direction: row;
    align-items: center; }


.b-like__icon {
    @include gradient-text(0, $firstGradientStart, $firstGradientEnd);
    margin-right: 5px;
    font-size: 14px;

    @include lg(font-size, 14px);
    @include md(font-size, 12px);
    @include sm(font-size, 14px);
    @include xs(font-size, 12px); }


.b-like__value {
    @include text($darkColor, 14px, 800, 1);

    @include lg(font-size, 14px);
    @include md(font-size, 12px);
    @include sm(font-size, 14px);
    @include xs(font-size, 12px); }
