.b-content {
    background-color: darken($lightColor, 2%);
    background-image: url('../img/content-bg.jpg');
    position: relative;

    @include sm-block() {
        &:after {
            content: '';
            width: 100%;
            height: 100%;
            @include fixed(0, auto, auto, 0);
            transition: all 400ms linear;
            opacity: 0;
            visibility: hidden;
            background-color: rgba(255, 255, 255, .6);
            z-index: 1; } } }

.b-content_sm-blackout:after {
    @include sm-block() {
        opacity: 1;
        visibility: visible; } }


.b-content__header {
    width: 100%;
    height: 60px;
    display: none;
    position: relative;

    @include sm-block() {
        display: block;
        z-index: 2; } }


.b-content__main {
    height: calc(100vh - 60px);
    margin-top: 60px;

    @include sm(margin-top, 0); }

