.b-single-model {}
.b-single-model__inner {
    @include row-flex();
    align-items: center; }


.b-single-model__features {
    @include col();
    @include size(6);
    margin-top: -51px;

    @include to(1500px) {
        @include size(4); }

    @include to(1300px) {
        @include size(5); }

    @include lg-block {
        @include size(12);
        margin-top: 60px; }

    @include sm-block() {
        margin-top: 30px; } }


.b-single-model__slider {
    @include col();
    @include size(6);
    position: relative;

    @include to(1500px) {
        @include size(7); }

    @include lg-block {
        @include size(12); }

    @include sm-block() {
        @include size(12); } }

.b-single-model__slider-items {
    filter: drop-shadow(0 2px 2px rgba(0,0,0,.1));

    @include sm(filter, none); }

.b-single-model__slider-item {
    outline: none; }

.b-single-model__img-box {
    width: 100%;
    padding-bottom: 100%;
    display: block;
    overflow: hidden;
    border-radius: 20px;
    outline: none;
    position: relative;
    transition: all .2s linear;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;

    &:after {
        content: '';
        width: 100%;
        height: 100%;
        @include absolute(0, auto, auto, 0);
        transition: all .1s linear;
        background-color: rgba(0, 0, 0, 0); } }

.b-single-model__slider:hover .b-single-model__img-box {

        &:after {
            background-color: rgba(0, 0, 0, .2); } }

.b-single-model__img {
    display: none !important; }

.b-single-model__footer {
    display: flex;
    flex-direction: row;
    padding: 15px 20px 0 20px;
    margin-top: 25px; }

.b-single-model__like {
    margin-left: auto; }

.b-single-model__slider-arrows {
    text-shadow: 2px 0 10px rgba(0, 0, 0, 0);
    transition: all .2s linear;
    opacity: 0; }

.b-single-model__slider:hover .b-single-model__slider-arrows {
    text-shadow: 2px 0 10px rgba(0, 0, 0, .1);
    opacity: 1; }


.b-single-model__slider-arrow-prev,
.b-single-model__slider-arrow-next {
    cursor: pointer;
    font-size: 36px;
    color: $lightColor;
    padding: 20px;
    @include absolute-offset(calc(50% - 9px), auto, auto, auto, -50%, 0);

    @include to(1400px) {
        font-size: 24px; }

    @include lg-block() {
        font-size: 28px; }

    @include md-block() {
        top: calc(50% - 7px); }

    @include sm-block() {
        top: calc(50% - 9px);
        font-size: 32px; }

    @include xs-block() {
        top: calc(50% - 7px);
        font-size: 24px; } }


.b-single-model__slider-arrow-prev {
    left: 20px;

    @include to(1400px) {
        left: 10px; }

    @include xs(left, 0px); }

.b-single-model__slider-arrow-next {
    right: 20px;

    @include to(1400px) {
        right: 10px; }

    @include xs(right, 0px); }

.b-single-model__slider-arrow-icon {}

.b-single-model__slider-dots {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin: 0 -3px;
    @include absolute(auto, auto, 2px, 20px);

    @include md(bottom, 1px);
    @include sm(bottom, 2px);
    @include xs(bottom, 1px);


    .slick-active {
        width: 12px;
        height: 12px;
        background: linear-gradient(0deg, $firstGradientStart, $firstGradientEnd);

        @include md-block() {
            width: 10px;
            height: 10px; }

        @include sm-block() {
            width: 12px;
            height: 12px; }

        @include xs-block() {
            width: 10px;
            height: 10px; } }

    li {
        width: 10px;
        height: 10px;
        font-size: 0;
        border-radius: 50%;
        border: none;
        outline: none;
        margin: 0 3px;
        background: linear-gradient(0deg, darken($lightColor, 20%), darken($lightColor, 10%));

        @include md-block() {
            width: 8px;
            height: 8px; }

        @include sm-block() {
            width: 10px;
            height: 10px; }

        @include xs-block() {
            width: 8px;
            height: 8px; }


        button {
            opacity: 0; } } }
