.b-features {}

.b-features__items {
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    flex-wrap: wrap;
    margin-top: -10px;
    margin-bottom: -10px;
    position: relative;

    @include to(1300px) {
        margin-top: -7.5px;
        margin-bottom: -7.5px; }

    @include lg-block() {
        margin-top: -10px;
        margin-bottom: -10px; }

    @include xs-block() {
        margin-top: -7.5px;
        margin-bottom: -7.5px; } }


.b-features__item-title,
.b-features__item-value {
    display: flex;
    flex-direction: row;
    margin-top: 10px;
    margin-bottom: 10px;

    @include to(1300px) {
        width: calc(50% - 5px);
        margin-top: 7.5px;
        margin-bottom: 7.5px; }

    @include lg-block() {
        width: calc(50% - 10px);
        margin-top: 10px;
        margin-bottom: 10px; }

    @include xs-block() {
        width: calc(50% - 5px);
        margin-top: 7.5px;
        margin-bottom: 7.5px; } }

.b-features__item-title {
    width: calc(40% - 15px);
    justify-content: flex-end;
    margin-right: 15px;

    @include to(1300px) {
        margin-right: 5px; }

    @include lg-block() {
        width: calc(45% - 15px);
        margin-right: 10px; }

    @include xs-block() {
        margin-right: 5px; } }


.b-features__item-value {
    width: calc(60% - 15px);
    justify-content: flex-start;
    margin-left: 15px;

    @include to(1300px) {
        margin-left: 5px; }

    @include lg-block() {
        width: calc(55% - 15px);
        margin-left: 10px; }

    @include xs-block() {
        margin-left: 5px; } }


.b-features__title {
    text-align: right;
    @include text($darkColor, 24px, 900, 1);
    position: relative;

    @include to(1500px) {
        font-size: 18px; }

    @include to(1300px) {
        font-size: 14px; }

    @include sm(font-size, 16px);

    @include xs(font-size, 12px); }


.b-features__value {
    text-align: left;
    @include text($darkColor, 36px, 900, 1);

    @include to(1500px) {
        font-size: 28px; }

    @include to(1300px) {
        font-size: 18px; }

    @include sm(font-size, 22px);

    @include xs(font-size, 16px); }


.b-features__title-line {
    width: 0;
    height: 4px;
    @include absolute(auto, auto, -10px, 0);
    background: linear-gradient(90deg, $firstGradientStart, $firstGradientEnd);

    @include to(1300px) {
        height: 3px;
        bottom: -7px; }

    @include lg-block() {
        height: 4px;
        bottom: -10px; }

    @include xs-block() {
        height: 3px;
        bottom: -5px; }

    @include xs-block() {
        height: 2px;
        bottom: -5px; } }


@for $i from 0 to 20 {
    .b-features__title-line_delay-#{$i + 1} {
        transition: all .4s #{($i * 0.05) + 's'} cubic-bezier(0.64, 0.57, 0.67, 1.53); } }

