.b-subtitle {
    padding-bottom: 30px;
    display: inline-flex;
    flex-direction: column;
    position: relative;

    @include lg(padding-bottom, 20px);

    @include xs(padding-bottom, 15px);

    &:before {
        content: '';
        width: 30%;
        height: 4px;
        @include absolute(auto, auto, 0, 0);
        background: linear-gradient(90deg, $firstGradientStart, $firstGradientEnd);

        @include xs(height, 2px); } }


.b-subtitle__texts {
    display: inline-flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-left: -5px;
    margin-right: -5px; }


.b-subtitle__text-first,
.b-subtitle__text-second {
    position: relative;
    margin-left: 5px;
    margin-right: 5px;
    @include text($darkColor, 36px, 400, 1);

    @include lg(font-size, 28px);

    @include md(font-size, 24px);

    @include sm(font-size, 22px);

    @include xs(font-size, 18px); }

.b-subtitle__text-first {
    font-weight: 900; }
