.b-contact {}

.b-contact__inner {
    height: 100%;
    display: flex;
    flex-direction: row;

    @include to(1400px) {
        justify-content: flex-end;
        flex-direction: column; }

    @include lg-block() {
        flex-direction: row;
        justify-content: initial; }

    @include xs-block() {
        align-items: center;
        flex-direction: column; } }

.b-contact__img-box {
    padding-top: 30px;
    min-height: 300px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    width: 35%;
    margin-right: 30px;
    position: relative;
    overflow: hidden;
    border-bottom-left-radius: 200px;
    border-bottom-right-radius: 200px;
    filter: drop-shadow(0 5px 10px rgba(0,0,0,.2));

    &:before {
        content: '';
        width: 100%;
        padding-bottom: 100%;
        @include absolute(auto, auto, 0, 0);
        border-radius: 50%;
        background: linear-gradient(0deg, $firstGradientStart, $firstGradientEnd); }

    @include to(1800px) {
        min-height: 200px; }

    @include to(1600px) {
        min-height: 150px; }

    @include to(1400px) {
        min-height: 175px;
        width: 60%;
        margin-right: 0; }

    @include lg-block() {
        min-height: 185px;
        width: 35%;
        margin-right: 30px; }

    @include md(min-height, 150px);

    @include sm-block() {
        min-height: 200px;
        width: 40%; }

    @include xs-block() {
        min-height: 155px;
        width: 60%;
        margin-right: 0; } }


.b-contact__img {
    width: 100%;
    display: block;
    position: relative;
    transition: all .6s ease-in-out;
    filter: drop-shadow(0 5px 10px rgba(0,0,0,.2));
    transform: translate3d(0, 15px, 0); }

.b-contact:hover .b-contact__img {
    filter: drop-shadow(0 5px 10px rgba(0,0,0,.4));
    transform: translate3d(0, 0px, 0); }


.b-contact__content-box {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 50px;

    @include to(1400px) {
        margin-top: 30px; }

    @include xs-block() {
        align-items: center;
        margin-top: 45px; } }


.b-contact__name {
    cursor: default;
    margin-bottom: 15px;
    @include text($darkColor, 34px, 900, 1);

    @include to(1800px) {
        font-size: 26px; }

    @include to(1600px) {
        font-size: 22px; }

    @include to(1400px) {
        font-size: 32px; }

    @include to(1250px) {
        font-size: 26px; }

    @include lg(font-size, 28px);
    @include md(font-size, 22px);
    @include sm(font-size, 24px);

    @include xs-block() {
        margin-bottom: 10px;
        font-size: 22px; } }

.b-contact__position {
    cursor: default;
    margin-bottom: 15px;
    @include text($darkColor, 16px, 900, 1);
    @include gradient-text(0deg, $firstGradientStart, $firstGradientEnd);

    @include to(1600px) {
        font-size: 14px; }

    @include to(1400px) {
        font-size: 16px; }

    @include to(1250px) {
        font-size: 14px; }

    @include lg(font-size, 16px);
    @include md(font-size, 14px);

    @include xs-block() {
        margin-bottom: 10px;
        font-size: 12px; } }


.b-contact__phone {
    cursor: pointer;
    margin-bottom: 15px;
    @include text($darkColor, 30px, 900, 1);
    letter-spacing: 0.25px;

    @include to(1800px) {
        font-size: 22px; }

    @include to(1600px) {
        font-size: 20px; }

    @include to(1400px) {
        font-size: 28px; }

    @include to(1250px) {
        font-size: 22px; }

    @include lg(font-size, 26px);
    @include md(font-size, 20px);
    @include sm(font-size, 20px);

    @include xs-block() {
        margin-bottom: 10px;
        font-size: 18px; } }


.b-contact__email {
    cursor: pointer;
    @include text($darkColor, 18px, 600, 1);

    @include to(1800px) {
        font-size: 16px; }

    @include to(1400px) {
        font-size: 18px; }

    @include to(1250px) {
        font-size: 16px; }

    @include lg(font-size, 18px);
    @include md(font-size, 14px);
    @include xs(font-size, 12px); }
